@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&family=Reggae+One&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    background-color: rgb(0, 0, 0);
}

.heading_style{
    padding: 30px 0;
    text-align: center;
    font-family: 'Reggae One', cursive;
    text-transform: uppercase;
    font-size: 20px;
    letter-spacing: 2px;
    font-weight: bolder;
    color: rgb(199, 199, 199);
    background-color: rgb(46, 46, 46);
    font-size: 1.5rem;
    margin-bottom: 30px;
}

footer{
    padding: 30px 0;
    text-align: center;
    font-family: 'Reggae One', cursive;
    text-transform: uppercase;
    font-size: 20px;
    letter-spacing: 2px;
    font-weight: bolder;
    color: rgb(255, 255, 255);
    background-color: rgb(0, 0, 0);
    /* font-size: 1.5rem; */
    margin-bottom: 30px;
}


.cards{
    width: 100%; 
    height: 100%;
    /* display: grid;
    grid-template-rows: 7fr;
    grid-template-columns: 7fr;
    align-items:initial;
    justify-content: center; */
}

.card{
    margin: 6%;
    transition: all 0.4s cubic-bezier(0.1755, 0.885, 0, 1);
    background-color: azure;
    /* width: 21.25%; */
    /* width: 80%; */
    /* height: 90%;  */
    border-radius: 12px;
    box-shadow: 0px 13px 10px -7px rgba(255, 255, 255, 0);
    float: left;
}

.card:hover{
    box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
    transform: scale(1.05, 1.05);
}

.card_img{
    width: 100%;
    height: 100%;
    /* border-top-left-radius: 12px; */
    border-top-right-radius: 12px;
}

.card_info{
    background-color: aliceblue;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
    border-bottom-left-radius: 12px;
    padding: 16px 24px 24px 24px;
}

.card_category{
    font-family: 'Reggae One', cursive;
    text-transform: uppercase;
    font-size: 20px;
    letter-spacing: 2px;
    font-weight: 500;
    color:  rgb(0, 30, 87);
    background-color: rgb(158, 210, 255);
}

.card_title{
    margin-top: 5px;
    margin-bottom: 10px;
    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
    background-color: aliceblue;
    text-transform: capitalize;
}

.info {
    margin-top: 5px;
    margin-bottom: 10px;
    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
    background-color: aliceblue;
    text-transform: capitalize;
}

button{
    padding: 5px 8px;
    font-family: 'Poppins', sans-serif;
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 2px;
    font-weight: bolder;
    color: black;
    background-color: rgb(194, 194, 194); 
    outline: none;
    border: 1px solid black;
    cursor: pointer;
}

@media (max-width: 768px) {
    .card{
        float: none;
        margin: 5%;
        width: 90%;
    }
}

